import { useDispatch } from 'react-redux'
import { increment } from '../slice'
import './menuStyle.scss'
import { FontColorsOutlined,WalletOutlined,AppstoreAddOutlined } from '@ant-design/icons';


function Menu() {
    const dispatch = useDispatch()
    return (
        <div className="Menu">
            <h2></h2>
            <div className='Menu-list'>                       
                <button onClick={() => dispatch(increment({ type: 'Text', id: Date.parse(new Date()), value: '' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>
                
                <button onClick={() => dispatch(increment({ type: 'Card', id: Date.parse(new Date()), size: 'small', title: '' }))}>
                    <WalletOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Button', id: Date.parse(new Date()), style: 'primary', value: '' }))}>
                    <AppstoreAddOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Radio', id: Date.parse(new Date()), text: '1' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Checkbox', id: Date.parse(new Date()), text: '1' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Switch', id: Date.parse(new Date()), text: '' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Picture', id: Date.parse(new Date()), text: '' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Textarea', id: Date.parse(new Date()), text: '' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>

                <button onClick={() => dispatch(increment({ type: 'Content', id: Date.parse(new Date()), text: '' }))}>
                    <FontColorsOutlined />
                    <span></span>
                </button>
            </div>
            <h2></h2>
            <div style={{ height: '10000px' }}>
                more
            </div>
        </div>
    );
}

export default Menu;
